button.buttonDrawSelected
{

    background-color: #567bc3;
    border: none;


    /* сверху | справа | снизу | слева */
    border: none;
    margin: 0 10px 0 0;

    cursor: pointer;


    height: 0;
    padding: 7%;

    background-size: 30px 30px;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 8px;


}

button.buttonDraw:hover{
    /*background-color: #567bc3;*/
}

button.buttonDraw{


    background-color: aliceblue;

    border: none;
    /* сверху | справа | снизу | слева */
    margin: 0 10px 0 0;

    cursor: pointer;


    height: 0;
    padding: 7%;

    background-size: 30px 30px;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 8px;

    box-shadow: 0 0 1em rgba(0, 0, 0, 0.3);

}
button.buttonEndDraw:hover{

    /*background-color: #567bc3;*/

}
button.buttonEndDraw{

    margin-left: auto;

    background-color: aliceblue;
    border: none;


    cursor: pointer;


    height: 0;
    padding: 7%;

    background-size: 30px 30px;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 8px;
    box-shadow: 0 0 1em rgba(0, 0, 0, 0.3);
}



