

.boxCanvas{
    position: absolute;

    left: 0%;
    display: flex;
    flex-direction: row;
    -ms-box-orient: horizontal;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -moz-flex;
    display: -webkit-flex;

}
div.canvasDraw{
    /*margin: 0 0 0 8%;*/
    position: absolute;
    /*left: 50%;*/
    /*marginRight: -50%;*/
    /*transform: translate(-50%);*/
}


div.chatPanel{
    position: absolute;

    right: 20px;
    margin-left: 10px;
    border-radius: 16px;


}



img.bg {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;

    /* Set up proportionate scaling */
    width: 105%;
    height: auto;

    /* Set up positioning */
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}


.panelUser{
    position: fixed;
    width: 100% ;
    top:0;
    left:0;
    /*background: aliceblue;*/
    background-color: rgba(240, 248, 255, 0.4);
    display: flex;
    margin-right: 20px;
}

.circle {


    position: relative;


    /*border:1px solid darkblue;*/
    border-radius: 50%;
    background: cornflowerblue;
    color: white;
    text-align: center;
    font-weight: bold;
    top: 50%;
    transform: translateY(-50%)

}
.chat{
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    /*overflow-x: hidden !important;*/
    display: flex !important;
    flex-direction: row-reverse !important;
    position: relative;
    width: 100%;

    flex: 0 0 auto;
    z-index: 1;
    align-items: flex-start;
    justify-content: flex-end;

}

.chatflex{
    flex: 0 0 auto;
    min-width: 100%;
    z-index: 1;
    display: flex;
    flex-direction: row-reverse;
    /*align-items: center;*/
    align-items: flex-start;
    justify-content: flex-end;

}



.panelChat{
    position: fixed;
    width: 100% ;

    left:0;
    /*background: aliceblue;*/
    background-color: rgba(240, 248, 255, 0.4);
}






